.activity {
  background: transparent;
  padding: 0.6rem;
  border-radius: 20px;
  color: #FCD1B0;
  border: 1.2px solid #FCD1B0;
  font-size: 0.9rem; }

.activity:hover {
  background: #fadac2 !important;
  outline: none !important;
  border: 1.2px solid #FCD1B0 !important; }

.activity:active, .activity:focus {
  background: #FCD1B0 !important;
  color: white !important;
  outline: none !important;
  border: 1.2px solid #FCD1B0 !important; }

.activityTitle {
  margin-left: 10rem !important;
  font-size: 1.3rem; }

.activityContainer {
  margin-left: 10rem !important; }

ul.entries {
  position: absolute;
  margin-left: 10rem;
  margin-top: 5rem; }

li.entry {
  min-height: 7.5rem;
  width: 40rem;
  list-style: none;
  background-color: #FCFBF8;
  box-shadow: #E5DDD0 0px 3px 5px 0px;
  border-radius: 4px; }

.entryMoodImg {
  width: 4.5rem; }

.dateEntry, .activityEntry {
  float: right;
  font-size: 1rem; }

.moodEntry {
  font-size: 2.3rem; }

.noteEntry {
  font-size: 1.5rem; }

.react-datepicker__input-container {
  margin-top: 1.5rem !important; }

.react-datepicker__input-container input {
  padding: 0.4rem;
  border: none;
  background-color: transparent;
  color: #212529;
  font-size: 1.2rem;
  text-align: center;
  border-radius: 2px; }

.react-datepicker__day--selected, .react-datepicker__day--in-selecting-range, .react-datepicker__day--in-range, .react-datepicker__month-text--selected, .react-datepicker__month-text--in-selecting-range, .react-datepicker__month-text--in-range {
  border-radius: 0.3rem;
  background-color: #BDE5AE !important;
  color: #FCFBF8 !important; }

.react-datepicker__day--selected:hover, .react-datepicker__day--in-selecting-range:hover, .react-datepicker__day--in-range:hover, .react-datepicker__month-text--selected:hover, .react-datepicker__month-text--in-selecting-range:hover, .react-datepicker__month-text--in-range:hover {
  background-color: #a1c693 !important; }

.moodList {
  list-style: none; }

li.mood {
  display: inline-block;
  height: 7rem;
  width: 7rem;
  cursor: pointer;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  transition-duration: 0.3s;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform; }

li.mood:hover {
  -webkit-transform: scale(1.06);
  transform: scale(1.06); }

.nav, .nav:hover, .nav:focus {
  text-decoration: none;
  color: #212529; }

.moodChooser {
  margin-top: 14rem !important;
  margin-left: 14rem !important; }

h1 {
  display: inline !important; }

img.nextButton {
